<template>
  <div class="type-nav">
    <div class="container">
<!-- 添加all、sort的一个父元素，方便在鼠标离开all的时候鼠标h3样式才变为没    -->
       <div @mouseleave="returnCurrent" @mouseenter="showList">
           <h2 class="all">全部商品分类</h2>
      <!--    三级联动    -->
             <!-- 传参到搜索页面，需要运用了自定义事件传参           -->
      <!--     列表展开和隐藏需要动画展现   -->
         <transition name="demo" >
<!--             name="animate__animated animate__bounce"
             enter-active-class="animate__bounceIn"
             leave-active-class="animate__bounceOut">-->

           <div class="sort" v-show="show">
      <!--      事件委派事件  -->
             <div class="all-sort-list2"  @click="backSearch">
               <div class="item bo" v-for="(c1,index) in categoryList" :key="c1.categoryId" :class="{active:current===index}">
                 <!--  当 current===index，就获得active类名        -->
                 <h3 @mouseenter="changeCurrent(index)">
      <!--              <a href="">{{c1.categoryName}}{{index}}</a>-->
                   <a :data-categoryName="c1.categoryName" :data-category1Id="c1.categoryId">{{c1.categoryName}}{{index}}</a>
                 </h3>
      <!--    二级、三级分类  用vue来控制二三级显示与隐藏，需要删掉css原来的鼠标经过事件        -->

                 <div class="item-list clearfix" :style="{display:current===index?'block':'none'}">
                   <div class="subitem">
                     <dl class="fore"  v-for="(c2,index) in c1.categoryChild" :key="c2.categoryId">
                       <dt>
                         <a :data-categoryName="c2.categoryName" :data-category2Id="c2.categoryId">{{c2.categoryName}}</a>
                       </dt>
                       <dd>
                         <em   v-for="(c3,index) in c2.categoryChild" :key="c3.categoryId">
                           <a :data-categoryName="c3.categoryName" :data-category3Id="c3.categoryId">{{c3.categoryName}}</a>
                         </em>
                       </dd>
                     </dl>
                   </div>
                 </div>
               </div>
             </div>
           </div >
         </transition>
       </div>
      <nav class="nav">
        <a href="#">服装城</a>
        <a href="#">美妆馆</a>
        <a href="#">尚品汇超市</a>
        <a href="#">全球购</a>
        <a href="#">闪购</a>
        <a href="#">团购</a>
        <a href="#">有趣</a>
        <a href="#">秒杀</a>
      </nav>

    </div>
  </div>
</template>

<script>
import {mapState} from "vuex";
/*import {throttle} from 'lodash'*/
/*import 'animate.css'*/
export default {
  name: "TypeNav",
  data(){
    return{
      current:-1,
      show:true
    }
  },
  methods: {
    /*在快速滑动的时候，由于没有节流，所以会出现卡顿*/
    changeCurrent(index) {
      //让当前的current=移动到h3获得的index，就改变current值
      this.current = index
   /*   console.log(index)*/
    },
    /*优点：解决了页面卡顿现象*/
    /*缺点：用了lodash,页面内容快速移开的时候，样式没消失*/
    /*    changeCurrent:throttle(function (index) {
          this.current=index
          console.log(index)
        },50),*/
    returnCurrent() {
      if (this.$route.name==='search'){
        this.show=false
      }
      this.current = -1
    },
    showList(){
      if (this.$route.name==='search'){
        this.show=true
      }
    },
    /*点击三级联动，页面跳转到search中 防止页面卡顿，运用编程式导航*/
    /*为了区分出点击的是不是a标签，运用了自定义事件，也方便把参数传过去。因为前面搜索也出现了跳转，也出现了传参跳转*/
    /*1.不能确定自己点击对象三级哪一级，用自定义解决*/
    /*   backSearch(event){
          this.$router.push({
            name:'search',
            query:{
              transmit:event.target.dataset.categoryname
            }
          })
      /!*    /!*为了获得点击对象*!/
          console.log(event.target.dataset.categoryname)*!/
        }*/
    /*2.教学方法*/
        backSearch(event) {
          let {categoryname, category1id, category2id, category3id} = event.target.dataset
          /*  let {categoryname,category1id,category2id,category3id}=event.target.dataset获得点击对象是{categoryname：'内容'}，{category1id：1 }....*/
          if (categoryname) {
            let location = {name: 'search'}
            //categoryname是读取data-categoryName的属性值
            let query = {transmit: categoryname}
            if (category1id) {
              query.Id1 = category1id
            } else if (category2id) {
              query.Id2 = category2id
            } else {
              query.Id3 = category3id
            }
           /* location.query = query
            this.$router.push(location)*/
              if(this.$route.params){
              location.params=this.$route.params
                location.query = query
                this.$router.push(location)
            }
          }


        },

  /*  3.自己改进方法,多而冗杂*/
       /* backSearch(event) {

      if (event.target.dataset.categoryname) {
        let location = {name: 'search'}
        let query = {transmit: event.target.dataset.categoryname}
        if (event.target.dataset.category1id) {
          query.Id1 = event.target.dataset.category1id
        } else if (event.target.dataset.category2id) {
          query.Id2 = event.target.dataset.category2id
        } else {
          query.Id3 = event.target.dataset.category3id
        }
        location.query = query
        this.$router.push(location)
      }

    }*/
  },

  computed:{
    ...mapState('homeAbout',['categoryList'])
  },
  mounted(){
   /* /!*...mapActions('homeAbout',['goryList'])*!/
    /!*由于请求服务器很多次，每次切换有该组件的时候都会不断发送请求，所以放在根组件app*!/
    this.$store.dispatch('homeAbout/goryList')*/
    /*默认在search中是隐藏*/
    if(this.$route.name==='search'){
      this.show=false
    }
  }
}
</script>

<style lang="less">
.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      background: #fafafa;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }


        }
        .active {
          background-color: darkseagreen;
        }
      }
    }
@keyframe ani {
  from {
    transform: translateY(-100%)
  }

  to {
    transform: translateY(0)
  }
}

  .demo-enter-active {
    animation: ani 0.5s
  }

  .demo-leave-active {
    animation: ani 1s reverse
  }
  }
}
</style>

