import {reqBanner, reqCategoryList, reqFloor} from "@/api";
export default {
    namespaced:true,
    actions:{
        /*当CategoryList组件一挂载，就向*/
      async  goryList({commit}){
            let result=await reqCategoryList()
          console.log(result)
          if (result.code===200){
              commit('GORYLIST',result.data)
          }
        },
        /*mock 轮播图*/
        async banner({commit}){
          let result=await reqBanner()
            console.log(result,'banner')
            if (result.code===200){
                commit('BANNER',result.data)
            }
        },
        /* floor组件轮播mock*/
        async  floor({commit}){
          const result=await reqFloor()
            console.log(result,'floor')
            if (result.code===200){
                commit('FLOOR',result.data)
            }

        }
    },
    mutations:{
        GORYLIST(state,value){
            state.categoryList=value.slice(0,15)
        },
        BANNER(state,data){
            state.bannerList=data
        },
        FLOOR(state,data){
            state.floorList=data
        }
    },
    getters:{},
    state:{
        /*如果服务器返回的是数组，categoryList就写成空数组，如果返回的是对象，就写成空对象*/
        categoryList:[],
        bannerList:[],
        floorList:[]
    }
}
