import {reqUserCode, reqUserRegister, reqUserLogin, reqUserInfo,  reqUserLogout} from "@/api";

export default {
    namespaced:true,
    actions:{
        /*获取验证码*/
     async   userCode({commit},phone){
            const result= await reqUserCode(phone)
         console.log(result,'userCode')
         if (result.code===200){
             commit('USERCODE',result.data)
             return 'ok'
         }else{
             return Promise.reject(new Error('false'))
         }
        },
     /*完成注册*/
        async   userRegister({commit},val){
            const result= await reqUserRegister(val)
            console.log(result,'userRegister')
            if (result.code===200){
                /*不用上传数据给mutations*/
                return 'ok'
            }else{
                return Promise.reject(new Error('false'))
            }
        },
        /*登录*/
        async   userLogin({commit},data){
            const result= await reqUserLogin(data)
            console.log(result,'userLogin')
            if (result.code===200){
                /*登陆完成时，获取该账号的token值，将其存在本地，后面哪个组件需要就发起验证token的服务器请求*/
                       commit('USERLOGIN',result.data.token)
                /*本地存储化*/
                sessionStorage.setItem('TOKEN',result.data.token)
                return 'ok'
            }else{
                return Promise.reject(new Error('false'))
            }
        },
        /*验证是否登录时保留了token,再vuex保存里面的详细数据，不用传参token，因为请求头会更新，需要再请求头设置config.headers.token=store.state.registerLogin.token*/
        async userInfo({commit}){
            const result=await reqUserInfo()
            console.log(result,'userInfo')
            if (result.code===200){
                commit('USERINFO',result.data)
                return 'ok'
            }else{
                return Promise.reject(new Error('false'))
            }
        },
        /*退出登录，清除token和userInfo*/
        async userLogout({commit}){
            const result=await reqUserLogout()
            console.log(result,'userLogout' )
            if (result.code===200){
                commit('USERLOGOUT')
            }else {
                return Promise.reject(new Error('false'))
            }
        }

    },
    mutations:{
        /*获取验证码*/
        USERCODE(state,code){
            state.code=code
        },
        /*登录*/
        USERLOGIN(state,token){
            state.token=token
        },
        /*验证是否登录时保留了token,再vuex保存里面的详细数据，*/
        USERINFO(state,userInfo){
            state.userInfo=userInfo
        },
        /*退出登录，清除token和userInfo*/
        USERLOGOUT(state){
          sessionStorage.removeItem('TOKEN')
            state.userInfo={}
            state.token=''
        }
    },
    getters:{},
    state:{
        /*验证码*/
            code:'',
        /*登录完成的信息*/
           token:sessionStorage.getItem('TOKEN'),
        userInfo:{}
    }
}