import axios from "axios";
import store from "@/store";
import nprogress from "nprogress";
import 'nprogress/nprogress.css'

const requests=axios.create({
    baseURL:'/api',
    timeout:5000
})

/*1.请求拦截器，发送请求前可以检测到*/
/*config 是配置对象，里面又header很重要*/
requests.interceptors.request.use((config)=>{
    /*当服务器有token时，保留该头请求头信息*/
    if (store.state.registerLogin.token){config.headers.token=store.state.registerLogin.token}
    nprogress.start()
    return config
})

/*2.响应拦截器 成功的回调=>服务器相应的数据发送回来可以检测得到和失败的回调*/
requests.interceptors.response.use((res)=>{
        nprogress.done()
        return res.data
    },
    (error)=>{

        return Promise.reject(new Error('false'))
    }
)
export default requests