import VueRouter from "vue-router";
import Vue from "vue";
import Home from "@/pages/Home";
import Search from "@/pages/Search";
import Register from '@/pages/Register'
import Login from "@/pages/Login";
import store from "@/store";
Vue.use(VueRouter)
/*(in promise) NavigationDuplicated: Avoided redundant navigation to current location: "/search/    错误*/
/*解决方法---重写push和replace*/
/*1.先保存原有的push和replace方法*/
let originPush=VueRouter.prototype.push
/*没用到replace方法*/
/*let originReplace=VueRouter.prototype.replace*/
VueRouter.prototype.push=function (location,resole,reject){
    if(resole&&reject){
        originPush.call(this,location,resole,reject)
    }else{
        originPush.call(this,location,()=>{},()=>{})
    }
}
/*VueRouter.prototype.replace=function (location,resole,reject){
    if(resole&&reject){
        originReplace.call(this,location,resole,reject)
    }else{
        originReplace.call(this,location,()=>{},()=>{})
    }
}*/
const router= new VueRouter({
    routes:[
        {
            path:'/home',
            name:'home',
            component:Home,
        },
        {
            path:'/search/:word?',
            name:'search',
            component:Search,
        },
        {
            path:'/register',
            name:'register',
            component:Register,
        },
        {
            path:'/login',
            name:'login',
            component:Login,
        },


        //重定向，项目跑起来，立马跑向某个路由页面
        {
            path:'/',
            redirect:'/home'
        }
    ]
})
router.beforeEach(async (to, from, next) => {
    /*登陆状态有token，不能去login和register页面*/
    /*登录状态都会存在token*/
    if (store.state.registerLogin.token) {
        if (to.path === '/login' || to.path === '/register') {
            next('/home')
        } else {
            /*登录状态有用户名和没用户名=>token过期*/
            /*登录状态有用户名*/
            console.log(store)
            if (store.state.registerLogin.userInfo.name) {
                next()
            } else {
                //登录状态没用户名
                //没有服务器申请验证token
                /*派发action到仓库存取信息再跳转*/
                try {
                    await store.dispatch('registerLogin/userInfo')
                    next()
                }catch (error){
                    //登录状态没用户名
                    //token过期，清除原先的token，也就是退出登录
                    await store.dispatch('registerLogin/userLogout')
                    next('/login')
                }
            }
        }

    }else {
        /*未登录状态都放行*/
        next()
    }
})
export default router