/*
main该文件是整个项目的入口文件
* */
/*引入vue文件*/
import Vue from 'vue'
/*引入包含所有组件的父元素 */
import App from './App.vue'

//引入Store文件
import store from './store'

/*关闭vue生产提示*/
Vue.config.productionTip = false


/*//1.引入路由系统文件
import VueRouter from "vue-router";
//2.使用VueRouter插件
Vue.use(VueRouter)*/
//3.引入路由的router文件
import router from "@/router";



/*引入api文件，获取访问服务器地址*/
/*注册全局路由组件，所有的组件都可以用，不用注册*/
/*全部商品*/
import TypeNav from "@/components/TypeNav";
Vue.component(TypeNav.name,TypeNav)

//1.全部引入element-ui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
//2.应用所需要的ElementUI;
Vue.use(ElementUI);



/*引入模拟后台数据mock*/
import '@/mock/mockServe'

//创建Vue实例对象
new Vue({
/*el:'#app',*/
  //将app组件放入容器中
  render: h => h(App),
//应用Store，让其他组件都能接受到vuex
store,
  //
  //应用router
router,
  //给Vue绑定全局事件总线，任意组件都可用，但是一般在多层之间使用
  beforeCreate() {
    Vue.prototype.$bus=this
  }
}).$mount('#app')


