<template>
  <header class="header">
    <!-- 头部的第一行 -->
    <div class="top">
      <div class="container">
        <div class="loginList">
          <p>尚品汇欢迎您！</p>
          <p v-if="!$store.state.registerLogin.userInfo.name">
            <span>请</span>
            <a @click="pushLogin">登录</a>
            <a @click="pushRegister" class="register">免费注册</a>
          </p>
          <p v-else>
            <a>{{$store.state.registerLogin.userInfo.name}}</a>
            <a class="register" @click="backLogin">退出登录</a>
          </p>
        </div>
        <div class="typeList">
          <a href="#">我的订单</a>
          <a href="#">我的购物车</a>
          <a href="#">我的尚品汇</a>
          <a href="#">尚品汇会员</a>
          <a href="#">企业采购</a>
          <a href="#">关注尚品汇</a>
          <a href="#">合作招商</a>
          <a href="#">商家后台</a>
        </div>
      </div>
    </div>
    <!--头部第二行 搜索区域-->
    <div class="bottom">
      <h1 class="logoArea">
        <a class="logo" title="尚品汇"  target="_blank" @click="backHome">
          <img src="./images/logo.png" alt="">
        </a>
      </h1>
      <div class="searchArea">
        <form action="#" class="searchForm">
          <input type="text" id="autocomplete" class="input-error input-xxlarge" v-model="keyword" />
          <button class="sui-btn btn-xlarge btn-danger" type="button" @click="pushSearch">搜索</button>
        </form>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "UserHeader",
  data(){
    return{
      keyword:'',
      isHid:true
    }
  },
  methods:{
    pushSearch(){
      let location= {name:'search',
          /*无法解决同种参数合并*/
          /*  query:{
              k:this.keyword.toUpperCase()
            },*/
          params:{
        word:this.keyword||undefined
      }}
      if (this.$route.query){
        location.query=this.$route.query
        this.$router.push(location)
      }
    },
    backHome(){
      this.$router.push('/home')
    },
    pushLogin(){
      this.$router.push('/login')
    },
    pushRegister(){
      this.$router.push('/register')
    },
    /*退出登录*/
    async backLogin(){
      try {
        await  this.$store.dispatch('registerLogin/userLogout')&&await this.$router.push('/home')
      }catch (error){alert(error.message)}
    }
  },
  mounted() {
    if (this.$store.state.registerLogin.userInfo===false){
      alert('true')
    }
  }


}
</script>

<style lang="less">
.header {
  &>.top {
    background-color: #eaeaea;
    height: 30px;
    line-height: 30px;

    .container {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .loginList {
        float: left;

        p {
          float: left;
          margin-right: 10px;

          .register {
            border-left: 1px solid #b3aeae;
            padding: 0 5px;
            margin-left: 5px;
          }
        }
      }

      .typeList {
        float: right;

        a {
          padding: 0 10px;

          &+a {
            border-left: 1px solid #b3aeae;
          }
        }

      }

    }
  }

  &>.bottom {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;

    .logoArea {
      float: left;

      .logo {
        img {
          width: 175px;
          margin: 25px 45px;
        }
      }
    }

    .searchArea {
      float: right;
      margin-top: 35px;

      .searchForm {
        overflow: hidden;

        input {
          box-sizing: border-box;
          width: 490px;
          height: 32px;
          padding: 0 4px;
          border: 2px solid #ea4a36;
          float: left;

          &:focus {
            outline: none;
          }
        }

        button {
          height: 32px;
          width: 68px;
          background-color: #ea4a36;
          border: none;
          color: #fff;
          float: left;
          cursor: pointer;

          &:focus {
            outline: none;
          }
        }
      }
    }
  }
}
</style>