/*mock模拟数据*/
/*1.list的轮播图banner*/
export  const reqBanner=()=>mockRequest({url:'/banner',method:'get'})
/*2.fool*/
export const reqFloor=()=>mockRequest({url:'/floor',method:'get'})
/*后台服务器请求统一处理*/
/*1.三级联动*/
//请求地址/api/product/getBaseCategoryList       请求方式GET
export const reqCategoryList=()=> request.get('/product/getBaseCategoryList')
/*2.获取注册组件验证码*/
//请求地址/api/user/passport/sendCode/{phone}      请求方式GET
export const reqUserCode=(phone)=>request({url:`/user/passport/sendCode/${phone}`,method:'get'})
/*3.完成用户注册*/
//请求地址/api/user/passport/register      请求方式post
export const reqUserRegister=(data)=>request({url:'/user/passport/register',data,method:'post'})
/*4.登录*/
//请求地址/api/user/passport/login      请求方式post 参数phone,password
export const reqUserLogin=(data)=>request({url:'/user/passport/login',data,method:'post'})
/*5.登录完成之后获取的登录信息*/
//请求地址/api/user/passport/auth/getUserInfo     请求方式get
export const reqUserInfo=()=>request({url:'/user/passport/auth/getUserInfo',method:'get'})
/*6.退出登录*/
//请求地址/api/user/passport/logout     请求方式get
export const reqUserLogout =()=>request({url:'/user/passport/logout ',method:'get'})